$prefix: '.wc-booster';
.block-editor-block-list__block li {
    position: relative;
}
#{$prefix}-quick-view {
    padding: 3px;
    cursor: pointer;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    gap: 5px;
    display: flex;
    align-items: center;
}

.components-base-control__label {
    text-transform: inherit !important;
    font-size: 14px !important;
    font-weight: 400 !important;
}
